              p.mb-0 John Bowlby (1907-1990), es un psicoanalista inglés que planteó, a través de sus estudios que, el apego (llevados a cabo en 1951 tras la segunda guerra mundial), es una conexión duradera generada por los seres humanos desde la primera infancia y es fundamental para el posterior desarrollo emocional del individuo. 
        p(data-aos="fade-left") Su teoría del apego, en colaboración con la psicóloga Mary Ainsworth, advierte que el fuerte vínculo emocional y físico con un cuidador (familia y padres) en los primeros años de vida, es fundamental para el desarrollo futuro, pues incidirá en la posibilidad de hacer amistades futuras, comprender el mundo con el apoyo de otros y generar equipo con colegas en cualquier etapa de la vida.
    
