          p.mb-0 Si el vínculo de apego es fuerte, se genera seguridad para interactuar, conocer y explorar el mundo; por el contrario, si es débil, se generan inseguridades que se reflejan en diversos contextos de la cotidianidad. 
    
        p(data-aos="fade-left") Para Serrano (2019), el procesamiento sensorial es la capacidad de registrar la información sensorial, interpretando y almacenando la información de los estímulos, para poder usarla en el futuro, como base para la percepción y la acción.
        p El niño, al interpretar la información sobre los estímulos que le rodean, necesita tener la capacidad para usar los mecanismos neuro- fisiológicos que regulan el estado interno, y mecanismos sensomotores.

