        .bg-color-4.p-4.mb-4(data-aos="fade-left")
          p.mb-0 Una de esas conductas es el apego, que hace parte de una de las respuestas instintivas humanas, así como el miedo, la separación y la inseguridad, que se activan como mecanismo de supervivencia, ante la ausencia del cuidador o de cambios, en el medio cercano. 
        p(data-aos="fade-left") Estos comportamientos innatos son denominados liberadores sociales, porque ayudan a asegurar la proximidad sobre su cuidador o figura de apego; uno de los ejemplos más claros es el llanto, que además de ser una alerta eficaz, tiene una incidencia demostrada científicamente, sobre la producción de leche materna, la atención del cuidador en momentos nocturnos y cuando se está aislado físicamente el uno del otro.
    
